@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
body {
	margin: 0;
	font-family: 'Roboto', sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

* {
    font-family: Verdana, Geneva, Tahoma, sans-serif;
}

.cases-types {
    border-radius: 14px;
    height:15vh;
}

.cases-types span {
    color: white;
}

.home-graph {
    height: 26vh;
}
* {
    font-family: Verdana, Geneva, Tahoma, sans-serif;
}

.cases-type {
    border-radius: 12px;
    font-size: 20px;
    cursor: pointer;
}

.cases-type:hover {
    box-shadow: 0 0 6px 6px #0004;
}
.dist-graph {
	height: 21vh;
}
 /* unvisited link */
a:link, a:visited, a:active {
	color: #ffff4d;
}

/* mouse over link */
a:hover {
	color: lime;
} 

.about {
	background: radial-gradient(ellipse at center, #BB1AD0cc, #4771C1cc);
	border: 10px double orangered;
	border-radius: 16px;
	color: #ffff4d;
	text-shadow: 2px 2px 8px black;
	padding: 10vh;
	text-align: center;
	-webkit-transform: translateY(50%);
	        transform: translateY(50%);
	width: -webkit-fit-content;
	width: -moz-fit-content;
	width: fit-content;
	margin: 0 auto;
}

.list {
	text-align: left;
	width: -webkit-max-content;
	width: max-content;
	margin: 0 auto;
}
.loader {
	height: 60vh;
	pointer-events: none;
	-webkit-animation: loader-spin infinite 20s linear;
	        animation: loader-spin infinite 20s linear;
}

.wrapper {
	height: 100vh;
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: center;
	        justify-content: center;
	-webkit-align-items: center;
	        align-items: center;
}

@-webkit-keyframes loader-spin {
	from {
		-webkit-transform: rotate(0deg);
		        transform: rotate(0deg);
	}
	to {
		-webkit-transform: rotate(360deg);
		        transform: rotate(360deg);
	}
}

@keyframes loader-spin {
	from {
		-webkit-transform: rotate(0deg);
		        transform: rotate(0deg);
	}
	to {
		-webkit-transform: rotate(360deg);
		        transform: rotate(360deg);
	}
}

